
import XLoading from '@/components/common/xLoading'
import Loading from 'vux/src/components/loading/index.vue'

import Popup from '@/components/common/popup'
import { isEmpty, getStamp } from '../utils/utils'
import { prefixUrl } from '@/utils/env'

export default {
components: {
XLoading,
Loading,
Popup
},
data () {
return {
showXLoading: false, // 
showLoading: false, // 
showLoadingText: '', // 
showPopup: false, // 
popupType: '', // 
popupMsg: '', // 
popupIcon: 0, // icon0,1,2
goodsList: [],
tel: '', // 
nickname: '' 
}
},
created () {
},
computed: {
},
methods: {
getStamp (val, type) {
return getStamp(val, type)
},
isEmpty (val) {
return isEmpty(val)
},
openPopupFun (type, msg, icon, back) { // 
this.popupType = type
this.popupMsg = msg
this.popupIcon = icon
this.showPopup = true
},
closePopupFun () { // 
this.showPopup = false
},
goToPage (val) { // 
console.log(val)
this.$router.push({path: prefixUrl + '/goodsInfo', query: {goods_id: val}})
}
}
}



