
import XLoading from '@/components/common/xLoading'
import Loading from 'vux/src/components/loading/index.vue'

import Popup from '@/components/common/popup'
import { isEmpty, getStamp } from '../utils/utils'

import { ajaxZixunTicketInfo } from '../api/getData'
var JsBarcode = require('jsbarcode')

export default {
components: {
XLoading,
Loading,
Popup
},
data () {
return {
showXLoading: true, // 
showPopup: false, // 
popupType: '', // 
popupMsg: '', // 
popupIcon: 0, // icon0,1,2
code: '', // 
ticket: {} 
}
},
created () {
if (!isEmpty(this.$route.query.code)) {
this.code = this.$route.query.code
} else {
this.$router.go(-1)
}
this.initData()
},
computed: {
},
updated () {
var self = this
self.showBarcode(self.code)
},
methods: {
getStamp (val, type) {
return getStamp(val, type)
},
isEmpty (val) {
return isEmpty(val)
},
showBarcode (val) {
if (val === 0) {
return
}
JsBarcode('#barcode', val, {
format: 'CODE128',
displayValue: true,
fontSize: 24,
lineColor: '#000'
})
},
openPopupFun (type, msg, icon, back) { // 
this.popupType = type
this.popupMsg = msg
this.popupIcon = icon
this.showPopup = true
},
closePopupFun () { // 
this.showPopup = false
},
beautifyTicketStatus () { // 
var timeNow = parseInt(new Date().getTime() / 1000)
var ticket = this.ticket
var status1 = ''
var status2 = ''
var status3 = ''
if (ticket.is_check === 1) { // 
if (ticket.is_through_ticket === 1) { // 
if (ticket.rest > 0) { // 
if (ticket.endtime >= timeNow) { // 
return status2
} else { // 
return status1
}
} else { // 
return status1
}
} else { // 
return status1
}
} else { // 
if (ticket.endtime >= timeNow) { // 
return status2
} else { // 
return status3
}
}
},
initData () { // 
var self = this
ajaxZixunTicketInfo(self.code).then(response => {
var data = response.data
console.log(data)
if (data.code === 200) {
if (!isEmpty(data.obj.ticket)) {
self.ticket = data.obj.ticket
console.log(self.ticket)
self.code = self.ticket.code
}
} else {
self.openPopupFun('alert', data.msg, 2)
}
self.showXLoading = false
})
}
}
}



