import fetch from '@/utils/fetch'
import { prefixApi } from '@/utils/env'

/**
 * [getJssdk jssdk]
 * @param  {[type]} val [url]
 * @return {[type]}
 */
export function ajaxZixunGetJssdk () {
  return fetch({
    url: prefixApi + '/ajaxZixunGetJssdk',
    method: 'post',
    params: {}
  })
}

